/* TresEnLinea.css */
.board-container {
    display: flex;
    flex-direction: column;
    align-items: center; /* Cambiado a 'center' */
    justify-content: center;
    height: 100vh;
  }
  
  .board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    gap: 5px;
    margin-top: 200px;
    justify-content: center;
  }
  
  .cell {
    border: 1px solid #333;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2em;
    cursor: pointer;
  }
  